<template>
    <section class="content">
        <div class="container-fluid">
            <div class="row">

                <div class="col-12">

                    <div class="card">
                        <div class="card-header">
                            <h3 class="card-title">Ticket List</h3>

                            <div class="card-tools">

                                <router-link to="/ticket/add" class="btn btn-sm btn-primary" @click="newModal">
                                    <i class="fa fa-plus-square"></i>
                                    Add New
                                </router-link>
                            </div>
                        </div>

                        <div class="card-body p-2">
                            <form autocomplete="off" @submit.prevent="getResults()" data-vv-scope="searchfr">
                                <div class="form-row">
                                    <div class="col">
                                        <v-select label="name" :get-option-label="getLabel"
                                            :reduce="(option) => option.id" :options="search_machines"
                                            placeholder="Enter machines ..." v-model="search.machine_id"
                                            name="machine_id">
                                        </v-select>
                                    </div>
                                    <div class="col">
                                        <v-select label="site_name" :reduce="(option) => option.id"
                                            :options="search_sites" placeholder="Select Site..."
                                            v-model="search.site_id" name="site_id">
                                        </v-select>
                                    </div>
                                    <div class="col">
                                        <v-select label="site_name" :options="['Open', 'Closed']"
                                            placeholder="Select Status..." v-model="search.status" name="status">
                                        </v-select>
                                    </div>

                                    <button type="submit" class="btn btn-success">Search</button>
                                </div>
                            </form>
                        </div>

                        <!-- /.card-header -->
                        <div class="card-body table-responsive p-0">
                            <table class="table table-hover">
                                <thead>
                                    <tr>
                                        <th>Ticket No </th>
                                        <th>Machine Name</th>
                                        <th>Machine Far No</th>
                                        <th>Site</th>
                                        <th>Created By</th>
                                        <th>Created Date</th>
                                        <th>Expected date</th>
                                        <th>Ticket Status</th>
                                        <th>Action</th>
                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="ticket in Tickets.data" :key="ticket.id">
                                        <td>{{ ticket.ticket_no }}</td>
                                        <td class="text-capitalize">{{ ticket.machine.name }}</td>
                                        <td>{{ ticket.machine.far_no }}</td>
                                        <td>{{ ticket.site ? ticket.site.site_name : '-' }}</td>
                                        <td>{{ ticket.created_by_name }}</td>
                                        <td>{{ ticket.created_at | myDate }}</td>
                                        <td>{{ ticket.likely_date | myDate }}</td>
                                        <td>{{ticket.status.toLowerCase()=='created by user'?'Open':ticket.status}}</td>
                                        <td>
                                            <a href="javascript:void(0);" class="btn btn-warning btn-sm"
                                                @click="viewModal(ticket)">
                                                View
                                            </a>
                                            <router-link :to="'/ticket/edit/' + ticket.id" class="btn btn-success btn-sm"
                                                v-if="ticket.status != 'Closed' && $gate.isAdmin()">Edit</router-link>
                                            <a href="javascript:void(0);" class="btn btn-danger btn-sm"
                                                @click="closeModal(ticket)" title="Close ticket"
                                                v-if="ticket.status != 'Closed' && $gate.isAdmin()">
                                                Close
                                            </a>
                                        </td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <!-- /.card-body -->
                        <div class="card-footer">
                            <pagination :data="Tickets" @pagination-change-page="getResults"></pagination>
                        </div>
                    </div>
                    <!-- /.card -->
                </div>
            </div>

        </div>


        <!-- Close Modal -->
        <div class="modal fade" id="closemodal" tabindex="-1" role="dialog" aria-labelledby="closemodal"
            aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">Close Ticket</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>

                    <form @submit.prevent="submitCloseTicket()" data-vv-scope="closeticket">
                        <div class="modal-body">
                            <div class="row">
{{verrors.closeticket}}
                                <div class="col-6" v-if="pop_ticket && pop_ticket.likely_date">
                                    <div class="form-group">
                                        <label>Actual Date</label>
                                        <datetime value-zone="Asia/Kolkata" v-model="formclose.actual_date"
                                            name="actual_date" input-class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.actual_date') }"
                                            v-validate="'required'" data-vv-as="Actual Date"
                                            :min-datetime="new Date(pop_ticket.created_at).toISOString()"
                                            :max-datetime="new Date().toISOString()" />
                                        <div v-if="verrors.has('closeticket.actual_date')"
                                            class="help-block invalid-feedback">{{
                                                    verrors.first('closeticket.actual_date')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6">
                                    <div class="form-group">
                                        <label>Machine Status</label>
                                        <select v-model="formclose.machine_status" name="machine_status"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.machine_status') }"
                                            v-validate="'required'" data-vv-as="machine status">
                                            <option value="Running">Running</option>
                                            <option value="Running with Problem">Running with Problem</option>
                                        </select>
                                        <div v-if="verrors.has('closeticket.machine_status')"
                                            class="help-block invalid-feedback">{{
                                                    verrors.first('closeticket.machine_status')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Estimated Material cost</label>
                                        <input v-model="pop_ticket.estimated_material_cost" type="text"
                                            class="form-control" readonly>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Actual Material cost</label>
                                        <input v-model="formclose.actual_material_cost" type="text"
                                            name="actual_material_cost" v-validate="'required'"
                                            data-vv-as="Actual Material cost" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.actual_material_cost') }">
                                        <div v-if="verrors.has('closeticket.actual_material_cost')"
                                            class="help-block invalid-feedback"> {{
                                                    verrors.first('closeticket.actual_material_cost')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label> Actual Material cost File</label>
                                        <input type="file" @change="uploadMaterialCost" multiple name="material_cost"
                                            ref="fileactualmaterial" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.material_cost') }"
                                            v-validate="''" data-vv-as="Actual Material cost" />

                                        <div class="clearfix" v-if="pop_ticket.actual_estimated_material_files">
                                            <a v-for="(item, x) in pop_ticket.actual_estimated_material_files"
                                                :key="item.id" :href="item.file" target="_blank">View
                                                {{ pop_ticket.actual_estimated_material_files.length > 1 ? x + 1 : ''
                                                }}</a>
                                        </div>

                                        <div v-if="verrors.has('closeticket.material_cost')"
                                            class="help-block invalid-feedback"> {{
                                                    verrors.first('closeticket.material_cost')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Estimated Service cost</label>
                                        <input v-model="pop_ticket.estimated_service_cost" type="text"
                                            class="form-control" readonly>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label>Actual Service cost</label>
                                        <input v-model="formclose.actual_service_cost" type="text"
                                            name="actual_service_cost" v-validate="'required'"
                                            data-vv-as="Actual Service cost" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.actual_service_cost') }">
                                        <div v-if="verrors.has('closeticket.actual_service_cost')"
                                            class="help-block invalid-feedback"> {{
                                                    verrors.first('closeticket.actual_service_cost')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-4">
                                    <div class="form-group">
                                        <label> Actual Service cost File </label>
                                        <input type="file" @change="uploadServiceCost" multiple name="service_cost"
                                            ref="fileactualservice" class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.service_cost') }"
                                            v-validate="''" data-vv-as="Actual Service cost" />

                                        <div class="clearfix" v-if="pop_ticket.actual_estimated_service_files">
                                            <a v-for="(item, x) in pop_ticket.actual_estimated_service_files"
                                                :key="item.id" :href="item.file" target="_blank">View
                                                {{ pop_ticket.actual_estimated_service_files.length > 1 ? x + 1 : ''
                                                }}</a>
                                        </div>

                                        <div v-if="verrors.has('closeticket.service_cost')"
                                            class="help-block invalid-feedback"> {{
                                                    verrors.first('closeticket.service_cost')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-6" v-if="pop_machine.is_consumtion_hr_run">
                                    <div class="form-group">
                                        <label>Current Run hour </label>
                                        <input v-model="formclose.current_run_hour" name="current_run_hour"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.current_run_hour') }"
                                            v-validate="{ required: true, decimal: 2, min_value: pop_machine.last_maintenances ? pop_machine.last_maintenances.run_hour : pop_machine.last_hr_run }"
                                            data-vv-as="Current Run hour" />
                                        <div v-if="verrors.has('closeticket.current_run_hour')"
                                            class="help-block invalid-feedback">
                                            {{
                                                    verrors.first('closeticket.current_run_hour')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_consumtion_km_run">
                                    <div class="form-group">
                                        <label> Current KM Run </label>
                                        <input v-model="formclose.current_km_run" name="current_km_run"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.current_km_run') }"
                                            v-validate="{ required: true, decimal: 2, min_value: pop_machine.last_maintenances ? pop_machine.last_maintenances.km_run : pop_machine.last_km_run }"
                                            data-vv-as="Current KM hour" />
                                        <div v-if="verrors.has('closeticket.current_km_run')"
                                            class="help-block invalid-feedback">{{
                                                    verrors.first('closeticket.current_km_run')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="pop_machine.is_production_fig">
                                    <div class="form-group">
                                        <label> Current Production fig </label>
                                        <input v-model="formclose.current_production_fig" name="current_production_fig"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.current_production_fig') }"
                                            v-validate="{ required: true, decimal: 2, min_value: pop_machine.last_maintenances ? pop_machine.last_maintenances.production_fig : pop_machine.periodic_maintenance_breakdown }"
                                            data-vv-as="Current Production fig" />
                                        <div v-if="verrors.has('closeticket.current_production_fig')"
                                            class="help-block invalid-feedback">{{
                                                    verrors.first('closeticket.current_production_fig')
                                            }}</div>
                                    </div>
                                </div>

                                <div class="col-6" v-if="formclose.ticket_type == 'Breakdown Maintenance'">
                                    <div class="form-group">
                                        <label>Production Loss</label>
                                        <input v-model="formclose.production_loss" name="production_loss"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.production_loss') }"
                                            v-validate="'required'" data-vv-as="Production Loss" />
                                        <div v-if="verrors.has('closeticket.production_loss')"
                                            class="help-block invalid-feedback">
                                            {{
                                                    verrors.first('closeticket.production_loss')
                                            }}</div>
                                    </div>
                                </div>
                                <div class="col-6" v-if="formclose.ticket_type == 'Breakdown Maintenance'">
                                    <div class="form-group">
                                        <label>Production loss amount</label>
                                        <input v-model="formclose.production_loss_amount" name="production_loss_amount"
                                            class="form-control"
                                            :class="{ 'is-invalid': verrors.has('closeticket.production_loss_amount') }"
                                            v-validate="'required'" data-vv-as="Current Production fig" />
                                        <div v-if="verrors.has('closeticket.production_loss_amount')"
                                            class="help-block invalid-feedback">{{
                                                    verrors.first('closeticket.production_loss_amount')
                                            }}</div>
                                    </div>
                                </div>


                            </div>
                        </div>

                        <div class="modal-footer">
                            <button type="button" class="btn btn-secondary" data-dismiss="modal">Close</button>
                            <button type="submit" class="btn btn-primary">Close Ticket</button>
                        </div>
                    </form>
                </div>
            </div>
        </div>
        <!--View Modal-->
        <div class="modal fade" id="viewmodal" tabindex="-1" role="dialog" aria-labelledby="viewmodal"
            aria-hidden="true">
            <div class="modal-dialog modal-lg" role="document">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">View Ticket</h5>
                        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
                            <span aria-hidden="true">&times;</span>
                        </button>
                    </div>
                    <div class="modal-body" v-if="pop_ticket">
                        <div class="row">



                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Machine Site</label>
                                    <v-select label="site_name" :reduce="(option) => option.id" :options="sites"
                                        placeholder="Choose Site ..." v-model="pop_ticket.site_id" disabled>
                                    </v-select>

                                </div>
                            </div>
                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Machine Category</label>
                                    <v-select label="name" :reduce="(option) => option.id" :options="categories"
                                        placeholder="Choose Category ..." v-model="pop_ticket.category_id" disabled>
                                    </v-select>

                                </div>
                            </div>
                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Machine Description</label>
                                    <v-select label="name" :reduce="(option) => option.id" :options="sub_categories"
                                        placeholder="Choose description ..." v-model="pop_ticket.sub_category_id"
                                        name="sub_category_id" :disabled="pop_ticket.id != ''">
                                    </v-select>
                                </div>
                            </div>
                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label> Machine : </label>
                                    {{ pop_ticket.machine.far_no }} {{ pop_ticket.machine.name }}
                                </div>
                            </div>

                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Maintenance Provider</label>
                                    {{ pop_ticket.maintenance_provider }}

                                </div>
                            </div>

                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label> {{ pop_ticket.maintenance_provider == 'user' ? 'user' : 'vendor' }}
                                    </label>
                                    <template v-if="pop_ticket.maintenance_provider == 'vendor'">
                                        <v-select label="name" :reduce="(option) => option.id" :options="users"
                                            placeholder="Enter users ..." v-model="pop_ticket.vendor_id"
                                            v-validate="{ required: pop_ticket.maintenance_provider == 'vendor' ? true : false }"
                                            disabled>
                                        </v-select>
                                    </template>
                                    <template v-else>
                                        <v-select label="name" :reduce="(option) => option.id" :options="users"
                                            placeholder="Enter users ..." v-model="pop_ticket.user_id" disabled>
                                        </v-select>
                                    </template>

                                </div>
                            </div>

                            <div class="col-6 bs"
                                v-if="pop_ticket.maintenance_provider == 'vendor' && pop_ticket.service_type">
                                <div class="form-group">
                                    <label>Service Type</label>
                                    {{ pop_ticket.service_type }}
                                </div>
                            </div>

                            <div class="col-6 bs"
                                v-if="pop_ticket.service_type == 'Free Service' && pop_ticket.free_service_no">
                                <div class="form-group">
                                    <label>Free service no:</label>
                                    {{ pop_ticket.free_service_no }}/{{ pop_ticket.machine.free_service_no }}
                                </div>
                            </div>

                            <div class="col-6 bs" v-if="pop_ticket.ticket_type">
                                <div class="form-group">
                                    <label>Ticket Type</label>
                                    {{ pop_ticket.ticket_type }}
                                </div>
                            </div>



                            <div class="col-3 bs" >
                                <div class="form-group">
                                    <label>Estimated Material cost</label>
                                    {{ pop_ticket.estimated_material_cost | toCurrency }}
                                    <p>
                                        <a :href="file.file" v-for="(file, x) in pop_ticket.estimated_material_files"
                                            :key="file.id" target="_blank">View
                                            {{ pop_ticket.estimated_material_files.length > 1 ? x + 1 : '' }}</a>
                                    </p>
                                </div>
                            </div>
                            <div class="col-3 bs" >
                                <div class="form-group">
                                    <label>Estimated Service cost : </label>
                                    {{ pop_ticket.estimated_service_cost | toCurrency }}
                                    <p>
                                        <a :href="file.file" v-for="(file, x) in pop_ticket.estimated_service_files"
                                            :key="file.id" target="_blank">View
                                            {{ pop_ticket.estimated_service_files.length > 1 ? x + 1 : '' }}</a>
                                    </p>
                                </div>
                            </div>
                            <div class="col-3 bs">
                                <div class="form-group">
                                    <label>Actual Material Cost : </label>
                                    <span v-if="pop_ticket.actual_material_cost"> {{
                                            pop_ticket.actual_material_cost | toCurrency
                                    }}</span>
                                    <p>
                                        <a :href="file.file"
                                            v-for="(file, x) in pop_ticket.actual_estimated_material_files"
                                            :key="file.id" target="_blank">View
                                            {{ pop_ticket.actual_estimated_material_files.length > 1 ? x + 1 : '' }}</a>
                                    </p>
                                </div>
                            </div>

                            <div class="col-3 bs">
                                <div class="form-group">
                                    <label>Actual Service Cost : </label>
                                    <span v-if="pop_ticket.actual_service_cost">{{
                                            pop_ticket.actual_service_cost | toCurrency
                                    }}</span>
                                    <p>
                                        <a :href="file.file"
                                            v-for="(file, x) in pop_ticket.actual_estimated_service_files"
                                            :key="file.id" target="_blank">View {{
                                                    pop_ticket.actual_estimated_service_files.length > 1 ?
                                                        x + 1 : ''
                                            }}</a>
                                    </p>
                                </div>
                            </div>




                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Ticket No : </label>
                                    {{ pop_ticket.ticket_no }}
                                </div>
                            </div>
                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Likely Date : </label>
                                    {{ pop_ticket.likely_date | myDate }}

                                </div>
                            </div>

                            <div class="col-4 bs">
                                <div class="form-group">
                                    <label>Actual Date : </label>
                                    {{ pop_ticket.actual_date | myDate }}
                                </div>
                            </div>



                            <div class="col-4 bs" v-if="pop_ticket.current_run_hour">
                                <div class="form-group">
                                    <label>Current Run Hour : </label>
                                    {{ pop_ticket.current_run_hour }}
                                </div>
                            </div>
                            <div class="col-4 bs" v-if="pop_ticket.current_km_run">
                                <div class="form-group">
                                    <label>Current KM Hour : </label>
                                    {{ pop_ticket.current_km_run }}
                                </div>
                            </div>
                            <div class="col-4 bs" v-if="pop_ticket.current_production_fig">
                                <div class="form-group">
                                    <label>Current Production Fig : </label>
                                    {{ pop_ticket.current_production_fig }}
                                </div>
                            </div>
                            <div class="col-4 bs" v-if="pop_ticket.production_loss">
                                <div class="form-group">
                                    <label>Production Loss : </label>
                                    {{ pop_ticket.production_loss }}
                                </div>
                            </div>
                            <div class="col-4 bs" v-if="pop_ticket.production_loss_amount">
                                <div class="form-group">
                                    <label>Production Loss Amount : </label>
                                    {{ pop_ticket.production_loss_amount | toCurrency }}
                                </div>
                            </div>





                        </div>
                    </div>
                </div>
            </div>
        </div>



    </section>
</template>

<script>
export default {
    data() {
        return {
            search_machines: [],
            search_sites: [],
            search: {
                page: 1,
                machine_id: '',
                site_id: ''
            },
            popticket: {},
            categories: [],
            sub_categories: [],
            sites: [],
            selected_machine: '',
            users: [],
            machines: [],
            Tickets: {},
            editmode: false,
            MaterialCost: null,
            ServiceCost: null,
            form: new Form({
                id: '',
                site_id: '',
                category_id: '',
                sub_category_id: '',
                free_service_no: '',
                ticket_type: 'Periodic Maintenance',
                service_type: 'Paid Service',
                machine_id: '',
                ticket_no: '',
                vendor_id: '',
                user_id: '',
                likely_date: '',
                estimated_material_cost: '',
                estimated_service_cost: '',
                maintenance_provider: 'vendor',
                problem_description: '',
                complaint_nature: 'Major',
            }),
            stages: [{
                id: '',
                stage_no: '',
                stage_date: '',
                description: '',
                file: '',
                new_file: ''
            }],

            formclose: new Form({
                id: '',
                machine_status: '',
                edit_type: 'closeticket',
                ticket_type: 'Periodic Maintenance',
                actual_date: '',
                actual_material_cost: '',
                actual_service_cost: '',
                current_run_hour: '',
                current_km_run: '',
                current_production_fig: '',
                production_loss: '',
                production_loss_amount: '',
                status: 'Closed',
                vendor_resolve_date: '',
                vendor_attain_date: '',
                vendor_response_date: '',
                vendor_call_date: '',
            }),
            pop_machine: '',
            pop_ticket: '',
            opn_stage: ['Vendor created', 'Vendor Assigned', 'Order issued', 'Payment under process', 'Payment Done', 'Logistics under process', 'Materials despatched', 'Material received at site', 'Maintainace under progress'],
            last_stage_index: 0,
            last_stage_date: '',
        }
    },
    methods: {
        getLabel(val) {
            if (typeof val === 'object') {
                return val.far_no + ' - ' + val.name;
            } else {
                return val;
            }
        },
        viewModal(ticket) {
            this.pop_ticket = ''

            axios.get("api/ticket/" + ticket.id)
                .then(res => {
                    this.pop_ticket = res.data.data
                })
                .catch(err => {
                    this.pop_ticket = ticket
                })


            axios.get("api/category/" + ticket.category_id)
                .then((res) => {
                    this.sub_categories = res.data.data;
                })
            $('#viewmodal').modal('show');
        },
        AddStage() {
            this.stages.push({
                id: '',
                stage_no: '',
                stage_date: '',
                description: '',
                file: '',
                new_file: ''
            });
        },
        async choose_stage_file(event, x) {
            this.stages[x].new_file = event.target.files[0];
        },

        async uploadMaterialCost(event) {
            this.MaterialCost = event.target.files
        },
        async uploadServiceCost(event) {
            this.ServiceCost = event.target.files
        },

        getResults(page = 1) {
            this.$Progress.start();
            this.search.page = page
            axios.get('/api/ticket', {
                params: this.search
            }).then(({
                data
            }) => (this.Tickets = data.data));
            this.$Progress.finish();
        },
        submitCloseTicket() {
            this.$forceUpdate();
            this.$validator.validateAll('closeticket').then(async (valid) => {
                if (valid) { 
                    let last_mDate=this.pop_machine.last_maintenances?this.pop_machine.last_maintenances.datetime:this.pop_machine.last_maintenance_date;
                     
                    if (last_mDate && this.formclose.actual_date && (Date.parse(moment(this.formclose.actual_date).format('MMM,DD YYYY')) < Date.parse(moment(last_mDate).format('MMM,DD YYYY')))) {
                        Swal.fire('You can not add previous date\'s data, Please remove record from maintenance list.','', 'warning');
                    } else {
                        let pop_loader = this.$loading.show({
                            container: this.$refs.vps_submit_pop,
                        });
                        this.formclose.put('/api/ticket/' + this.formclose.id)
                            .then((response) => {
                                if (response.data.success) {
                                    $('#closemodal').modal('hide');
                                    Toast.fire({
                                        icon: 'success',
                                        title: response.data.message
                                    });
                                    this.upload_files(this.formclose.id, 'actual')
                                    this.$validator.reset();
                                    this.formclose.reset();
                                    this.loadTickets();
                                }
                                pop_loader.hide()
                            })
                            .catch(() => {
                                pop_loader.hide()
                                if (typeof err.response.data != 'undefined') {
                                    this.$setErrorsFromResponse(err.response.data);
                                }
                            })
                    }
                }
            });
        },

        closeModal(ticket) {
            this.$validator.pause();
            $('#closemodal').modal('show');
            this.$validator.reset();
            this.formclose.reset();
            this.$refs.fileactualservice.value = null;
            this.$refs.fileactualmaterial.value = null;
            this.pop_ticket = ticket
            this.pop_machine = ticket.machine
            this.formclose.fill(ticket);
            this.formclose.edit_type = 'closeticket'
            this.$validator.resume();
        },
        editModal(ticket) {
            this.editmode = true;
            this.$validator.pause();
            this.form.reset();
            this.pop_ticket = ticket
            if (this.$gate.isAdmin()) {
                this.$refs.fileservice.value = null;
                this.$refs.filematerial.value = null;
            }
            this.MaterialCost = null
            this.ServiceCost = null
            $('#addNew').modal('show');
            this.form.fill(ticket);
            this.form.likely_date = ticket.likely_date ? new Date(ticket.likely_date).toISOString() : ''
            this.stages = []
            if (ticket.stages && ticket.stages.length > 0) {
                this.stages = ticket.stages
            }
            this.$validator.resume();
        },
        newModal() {
            this.stages = []
            this.AddStage()
            this.editmode = false;
            this.$validator.reset();
            this.$validator.pause();
            this.form.reset();
            if (this.$gate.isAdmin()) {
                this.$refs.fileservice.value = null;
                this.$refs.filematerial.value = null;
            }
            this.MaterialCost = null
            this.ServiceCost = null
            $('#addNew').modal('show');
            this.$validator.resume();
        },

        loadTickets() {
            // if (this.$gate.isAdmin()) {
            axios.get("/api/ticket", {
                params: this.search
            }).then(({
                data
            }) => (this.Tickets = data.data));
            //   }
        },

        async upload_files(id, isactual = false) {
            let fl = 0
            if (this.MaterialCost) {
                this.MaterialCost.forEach(file => {
                    var fd = new FormData();
                    fd.append(`file`, file);
                    if (isactual) {
                        fd.append(`file_type`, 'Actual Estimated Material');
                    } else {
                        fd.append(`file_type`, 'Estimated Material');
                    }
                    fd.append(`ticket_id`, id);
                    axios.post('api/ticket/files', fd).then((res) => {
                        console.log('gallery uploaded')
                    })
                });
            }
            if (this.ServiceCost) {
                this.ServiceCost.forEach(file => {
                    var fd = new FormData();
                    fd.append(`file`, file);
                    if (isactual) {
                        fd.append(`file_type`, 'Actual Estimated Service');
                    } else {
                        fd.append(`file_type`, 'Estimated Service');
                    }
                    fd.append(`ticket_id`, id);
                    axios.post('api/ticket/files', fd).then((res) => {
                        console.log('gallery uploaded')
                    })
                });
            }
        },

        async upload_stages(id) {
            if (this.stages) {
                this.stages.forEach(stage => {
                    var fd = new FormData();
                    fd.append(`id`, stage.id);
                    fd.append(`stage_no`, stage.stage_no);
                    fd.append(`stage_date`, stage.stage_date);
                    fd.append(`description`, stage.description);
                    //fd.append(`new_file`, stage.new_file);
                    // fd.append(`file`, stage.file);
                    fd.append(`ticket_id`, id);
                    axios.post('api/ticket/stages', fd).then((res) => {
                        console.log('stages uploaded')
                    })
                });
            }
        },

        submitTicket() {
            this.$validator.validateAll().then(async (valid) => {
                if (valid) {
                    let pop_loader = this.$loading.show({
                        container: this.$refs.vps_submit_pop,
                    });
                    if (this.form.id) {
                        this.form.put('/api/ticket/' + this.form.id)
                            .then((response) => {
                                if (response.data.success) {
                                    $('#addNew').modal('hide');
                                    Toast.fire({
                                        icon: 'success',
                                        title: response.data.message
                                    });
                                    this.upload_stages(this.form.id)
                                    this.upload_files(this.form.id)
                                    this.loadTickets();
                                }
                                pop_loader.hide()
                            })
                            .catch(() => {
                                pop_loader.hide()
                                if (typeof err.response.data != 'undefined') {
                                    this.$setErrorsFromResponse(err.response.data);
                                }
                            })

                    } else {
                        this.form.post('/api/ticket')
                            .then((response) => {
                                if (response.data.success) {
                                    $('#addNew').modal('hide');
                                    Toast.fire({
                                        icon: 'success',
                                        title: response.data.message
                                    });

                                    this.upload_files(response.data.data.id)
                                    this.loadTickets();
                                }
                                pop_loader.hide()
                            })
                            .catch(() => {
                                pop_loader.hide()
                                if (typeof err.response.data != 'undefined') {
                                    this.$setErrorsFromResponse(err.response.data);
                                }
                            })
                    }

                }
            });


        },

        async check_free_service() {
            if (this.selected_machine) {
                if (this.selected_machine.completed_free_service >= this.selected_machine.free_service_no) {
                    this.form.service_type = 'Paid Service';
                    this.form.free_service_no = ''
                } else {
                    this.form.free_service_no = (this.selected_machine.completed_free_service + 1)
                }


                if (this.selected_machine && this.selected_machine.machine_type == 'New' && this.form.service_type == 'Free Service') {
                    //  this.selected_machine.free_service_no
                }
            }
        },

        async get_machine() {
            axios.get("/api/ticket_machine", {
                params: {
                    site_id: this.form.site_id,
                    category_id: this.form.category_id,
                    sub_category_id: this.form.sub_category_id,
                }
            }).then(response => {
                this.machines = response.data.data;
            }).catch(() => console.warn('Oh. Something went wrong'));
        }

    },
    mounted() {

    },
    created() {
        this.loadTickets();
    },
    beforeCreate() {
        axios.get("api/get_pre_machine")
            .then((res) => {
                this.categories = res.data.data.Category;
                this.sites = res.data.data.sites;
            })
        axios.get("/api/initial_ticket").then(response => {
            this.users = response.data.data.vendors;
            this.machines = response.data.data.machines;
            this.search_machines = response.data.data.machines;
            this.search_sites = response.data.data.sites;
        }).catch(() => console.warn('Oh. Something went wrong'));
    },
    watch: {
        async 'form.sub_category_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },
        async 'form.site_id'(n, o) {
            if (n) {
                this.get_machine()
            }
        },

        'form.category_id': {
            handler: function (n, o) {

                if (n) {
                    axios.get("api/category/" + n)
                        .then((res) => {
                            this.sub_categories = res.data.data;
                        })
                    this.get_machine()
                }
            },
            deep: true,
            initial: true
        },
        stages: {
            handler: function (n, o) {
                if (n && n.length > 0) {
                    let la = n[n.length - 1];
                    if (la.stage_no)
                        this.last_stage_index = this.opn_stage.indexOf(la.stage_no);
                    if (la.stage_date)
                        this.last_stage_date = new Date(la.stage_date).toISOString();
                } else {
                    this.last_stage_index = 0;
                    if (this.pop_ticket && this.pop_ticket.created_at) {
                        this.last_stage_date = new Date(this.pop_ticket.created_at).toISOString();
                    } else {
                        this.last_stage_date = new Date().toISOString();
                    }

                }
            },
            deep: true,
            initial: true,
        },

        async 'form.maintenance_provider'(n) {
            if (n != 'vendor') {
                this.form.service_type = 'Paid Service'
            }
            axios.get("api/user/list", {
                    params: {
                        type: n
                    }
                })
                .then(response => {
                    this.users = response.data.data;
                }).catch(() => console.warn('Oh. Something went wrong'));
        },

        async 'form.machine_id'(n) {
            this.selected_machine = this.machines.find(x => x.id === n);
            await this.check_free_service()
        },
        async 'form.service_type'(n) {
            if (n == 'Free Service') {
                await this.check_free_service()
            }
        }

    },
}
</script>
<style>
.bs {
    border: 1px solid #808080a8;
    border-radius: 5px;
}
</style>
